import { IPluginBridge, INemoPlugin } from '../../src/webview/bridge/plugin/interface';
class MockPlugin implements INemoPlugin {
  private bridge?:IPluginBridge;

  public name = 'mock_plugin';

  public init(nemo_plugin_bridge:IPluginBridge) {
    this.bridge = nemo_plugin_bridge;

    this.bridge.add_sync_msg_listener((msg) => {
      console.log('Mock Plugin received sync msg', msg);

      return 'Plugin Get Sync Msg';
    });

    this.bridge.add_async_msg_listener(async(msg) => {
      console.log('Mock Plugin received async msg', msg);

      return 'Plugin Get Async Msg';
    });

    this.create_test_panel();

    return {
      is_success: true,
      error_msg: '',
    };
  }

  public test_sync() {
    console.log('Test post sync msg');
    if (!this.bridge) { return; }

    const result = this.bridge.post_sync_msg_to_app(
      this.name,
      {
        type: 'plugin_posting_sync_msg',
        payload: 'sync msg payload',
      },
    );

    console.log('sync result', result);
  }

  public test_async() {
    console.log('Test post async msg');
    if (!this.bridge) { return; }

    this.bridge.post_async_msg_to_app(
      this.name,
      {
        type: 'plugin_posting_async_msg',
        payload: 'async msg payload',
      },
    ).then((res) => {
      console.log('async result', res);
    }).catch((err) => {
      console.error(err);
    });
  }

  private async create_test_panel() {
    const root = document.createElement('div');
    root.id = 'mock_plugin_root';
    root.style.position = 'fixed';
    root.style.backgroundColor = 'skyblue';
    root.style.zIndex = '9999';
    root.style.top = '0';
    root.style.left = '0';

    document.body.appendChild(root);

    import('./app');
  }
}

const mock_plugin = new MockPlugin();

(window as any).mock_plugin = mock_plugin;

const nemo_plugin_bridge = (window as any).nemo_plugin_bridge;

nemo_plugin_bridge.add_plugin_candidate(mock_plugin.name, mock_plugin);
